<script>
  let base_url = "http://compile-dev-alb-713890182.ap-northeast-1.elb.amazonaws.com/book?orderBy=review_average&order=desc"
//   let url = "http://api.compileapp.net/book?orderBy=review_average&order=desc"
//   let url = "http://localhost:8081/book?orderBy=review_average&order=desc"

  export default {
    data: () => ({
      books: [],
      tags: ["python", "Java"],
      selected_tag: null,
    }),
    methods: {
        getBooks(url) {
            fetch(url)
            .then( response => {
                return response.json()
            })
            .then( json => {
                this.books = json.books
            })
        },
        getHomeBooks() {
            this.getBooks(base_url)
        },
    },
    created() {
        this.selected_tag = "python"
    },
    watch: {
        selected_tag: function(tag) {
            console.log(tag)
            let url = base_url + "&tag=" + tag
            this.getBooks(url)
        },
    }
  }
</script>